<template>
  <el-select v-model="selected" multiple placeholder="Select" @change="selectedVal">
    <el-option 
        v-for="label in labels" 
        :key="label._id" 
        :label="label.name"
        :value="label.name">
    </el-option>
  </el-select>
</template>
 
<script>

export default {
  props:{
    toy:{
      type:Object,
      default: null
    }
  },
 
  data() {
    return {
      selected:null,
      // options: [],
      labels: null
    };
  },
  created() {
    this.labels = this.$store.getters.getLabels;
    if (this.toy) this.selected = this.toy.labels
  },
  methods:{
      selectedVal(){
        this.$emit('updateLabels', this.selected)
      }
  },
};
</script>
