<template>
   <div class="img-upload-container">
       <label class="clickable" v-if="!isLoading">
           <img :src="require('../assets/imgs/upload.png')" alt="" />
       <input type="file" id="uploadImg" @change="onUploadImg" hidden />
       </label>
       <img v-else :src="require('../assets/imgs/loader.gif')" alt="" />
   </div>
</template>

<script>
import { uploadImg } from '@/services/imgUpload.service.js'

export default {
data() {
    return {
        isLoading: false
    }
},
methods: {
    async onUploadImg(ev) {
        this.isLoading = true
        let res = await uploadImg(ev);
        // console.log(res);
        this.$emit('onSaveImg', res.url)
        this.isLoading = false
    }
}
}
</script>

<style>
.clickable {
    cursor: pointer;
}
img {
    height: 200px;
}
</style>