

<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    labels() {
      return this.$store.getters.getLabels;
    },
    price(){
        if (this.toys && this.labels){

            return this.toys.filter((toy) =>
          this.labels.some((filterLabel) =>
            toy.labels.includes(filterLabel)
          ))
        }
    }
  },
  mounted() {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          label: "GitHub Commits",
          backgroundColor: "#f87979",
          data:this.price
        }
      ]
    });
  }
};
</script>

