<template>
  <div class="container home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <ul class="review-list">
      <li v-for="review in reviews" :key="review._id">
        <p>
          About
          <router-link :to="`toy/${review.aboutToy._id}`">{{review.aboutToy.name}}</router-link>
        </p>
        <h3>{{review.txt}}</h3>
        <section v-if="review.imgUrls">
          <img v-for="imgUrl in review.imgUrls" :key="imgUrl" :src="imgUrl" alt />
        </section>
        <p>
          By
          <router-link :to="`user/${review.byUser._id}`">{{review.byUser.fullname}}</router-link>
        </p>
        <hr />
      </li>
    </ul>
    <hr />
    <form v-if="loggedInUser" @submit.prevent="addReview()">
      <h2>Your gossip:</h2>
      <select v-model="reviewToEdit.aboutToyId">
        <option v-for="toy in toys" :key="toy._id" :value="toy._id">{{toy.name}}</option>
      </select>
      <textarea placeholder="Your Opinion Matters..." v-model="reviewToEdit.txt"></textarea>
      <div class="upload">
        <!-- <h1>Let's upload some images!</h1> -->
        <img-upload @onSaveImg="saveImg"></img-upload>
        <section class="img-list">
          <img v-for="imgUrl in reviewToEdit.imgUrls" :key="imgUrl" :src="imgUrl" alt />
        </section>
      </div>
      <button>Save</button>
    </form>
  </div>
</template>

<script>
import imgUpload from "@/cmps/img-upload.cmp";

export default {
  name: "home",
  data() {
    return {
      reviewToEdit: {
        txt: "",
        aboutToyId: null,
        imgUrls: []
      },
      // imgUrls: []
    };
  },
  computed: {
    reviews() {
      console.log("reviews", this.$store.getters.reviews);
      return this.$store.getters.reviews;
    },
    toys() {
      console.log(this.$store.getters.filteredToys);
      return this.$store.getters.filteredToys;
    },
    loggedInUser() {
      console.log(this.$store.getters.user);
      return this.$store.getters.user;
    }
  },
  created() {
    this.$store.dispatch({ type: "loadUser" });
    this.$store.dispatch({ type: "loadReviews" });
    this.$store.dispatch({ type: "loadToys" });
  },
  methods: {
    async addReview() {
      await this.$store.dispatch({
        type: "addReview",
        review: this.reviewToEdit
      });
      this.reviewToEdit = { txt: "", aboutToyId: null, imgUrls:[] };
    },
    saveImg(imgUrl) {
      this.reviewToEdit.imgUrls.push(imgUrl);
      console.log(this.reviewToEdit);
    }
  },
  components: {
    imgUpload
  }
};
</script>
