import Vue from 'vue';
import VueRouter from 'vue-router';
import home from '../pages/home.vue';
import about from '../pages/about.vue';
import toyApp from '../pages/toy-app.vue';
import toyDetails from '../pages/toy-details.vue'
import toyEdit from '../pages/toy-edit.vue'
import login from '../pages/login.vue'
import signup from '../pages/signup.vue'
import userList from '../pages/user-list.vue'
import dashboard from '../pages/dashboard.vue'
import reviewApp from '../pages/review-app.vue'



Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: home,
  },
  {
    path: '/login',
    name: 'Login',
    component: login,
  },
  {
    path: '/userlist',
    name: 'userList',
    component: userList,
  },
  {
    path: '/signup',
    name: 'signup',
    component: signup,
  },
  {
    path: '/about',
    name: 'About',
    component: about,
  },
  {
    path: '/review',
    name: 'review',
    component: reviewApp
  },
  {
    path: '/toy',
    name: 'toy-app',
    component: toyApp,
  },
  {
    path: '/dashboard',
    name: 'charts',
    component: dashboard,
  },
  {
    path: '/toy/edit/:toyId?',
    name: 'toy-edit',
    component: toyEdit,
  },
  {
    path: '/toy/:toyId',
    name: 'toy-details',
    component: toyDetails,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
