<template>
  <section class="toy-details">
    <router-link to="/toy">Back</router-link>
    <div class="toy-details" v-if="toy">
      <h3>
        <span>{{toy.name}}</span>
      </h3>
      <p>Price ${{toy.price}}</p>
      <p>
        <span v-if="toy.labels">Labels:</span>
        <span v-for="(label,idx) in toy.labels" :key="label">
          {{label}}
          <span v-show="idx!==toy.labels.length-1">|</span>
        </span>
      </p>
      <p>{{toy.inStock ? 'In Stock' : 'Currently out of Stock'}}</p>
      <h3 v-if="toy.review">Reviews:</h3>
      <span v-for="(review,idx) in toy.review" :key="idx">
        <h4>Review n. {{idx+1}}</h4>
        <p>{{review.txt}}</p>
        <p>By: {{review.byUser.fullname}}</p>
      </span>
      <p>
        <span>Created At:</span>
        {{timeToShow}}
      </p>
      <small>Id: {{toy._id}}</small>
    </div>
  </section>
</template>

<script>
import { eventBusService, showMsg } from "../services/event-bus.service.js";
import { toyService } from "../services/toy.service.js";
import { userService } from "../services/user.service.js";

export default {
  data() {
    return {
      toy: null
    };
  },
  async created() {
    const { toyId } = this.$route.params;
    try{
      const toy =await toyService.getById(toyId)
      // console.log('toy', toy);
      this.toy = toy;
    }
    catch(err){
      console.log('problem with getting toy in detailed', err);
    }
  },
  methods: {},
  computed: {
    timeToShow() {
      return new Date(this.toy.createdAt).toLocaleString().substring(0, 17);
    },

  }
};
</script>
