const TOY_URL = 'http://localhost:3030/api/toy/';
// import axios from 'axios';
import Axios from 'axios';
var axios = Axios.create({ withCredentials: true, });

export const toyService = {
  query,
  remove,
  save,
  getEmptyToy,
  getById,
  getLabels,
};

window.cs = toyService;

async function query(filterBy = {}) {
  try {
    const toys = await axios.get(TOY_URL, { params: filterBy });
    return toys.data;
  } catch (err) {
    console.log('err with query toys', err);
  }
}

async function getLabels() {
  try {
    const labels = await axios.get(TOY_URL + 'label');
    return labels.data;
  } catch (err) {
    console.log('err while getting labels', err);
  }
}

async function getById(id) {
  try {
    const toy = await axios.get(TOY_URL + id);
    return toy.data;
  } catch (err) {
    console.log('err while getting toy id:' + id, err);
  }
}

async function remove(toyId) {
  try {
    await axios.delete(TOY_URL + toyId);
    return toyId;
  } catch (err) {
    console.log('err while removing toy with id:' + toyId, err);
  }
}

async function save(toy) {
  if (toy._id) {
    try {
      const savedToy = await axios.put(TOY_URL + `${toy._id}`, toy);
      // console.log(savedToy);
      return savedToy.data;
    } catch (err) {
      console.log('err while updating toy:' + toy, err);
    }
  } else {
    try {
      const savedToy = await axios.post(TOY_URL, toy);
      return savedToy.data.ops[0];
    } catch (err) {
      console.log('err while saving toy:' + toy, err);
    }
  }
}

function getEmptyToy() {
  return {
    // _id: '',
    name: '',
    price: null,
    labels: [],
    createdAt: null,
    inStock: true,
  };
}
