<template>
  <section class="toy-preview">
    <div class="preview-container">
      <h4>
        <span>{{toy.name}}</span>
        <div>${{toy.price}}</div>
      </h4>
      <div class="preview-actions">
        <router-link :to="'/toy/'+toy._id">Details</router-link>
        <router-link :to="'/toy/edit/'+toy._id">Edit</router-link>
        <button @click="removeToy">Delete</button>
      </div>
    </div>
  </section>
</template>

<script>
import { eventBusService } from "../services/event-bus.service.js";

export default {
  props: {
    toy: {
      type: Object
    }
  },
  data() {
    return {};
  },

  created() {},
  methods: {
    // toggleDone() {
    //   eventBusService.$emit('changedDone', this.toyToEdit);
    // },
    editToy() {
      eventBusService.$emit("editToy", this.toyToEdit);
    },
    removeToy() {
      eventBusService.$emit("removeToy", this.toyToEdit._id);
    }
  },
  computed: {
    toyToEdit() {
      return JSON.parse(JSON.stringify(this.toy));
    }
  }
};
</script>