<template>
  <div id="nav" class="main-header">
    <div>
    <router-link to="/">Home</router-link>
    <router-link to="/about">About</router-link>
    <router-link to="/toy">Toy shop</router-link>
    </div>
    <div>
      <router-link v-if="!user" to="/login">Login</router-link>
      <span v-if="user">{{user.fullname}}</span>
      <router-link v-if="user && user.isAdmin" to="/userlist">Users List</router-link>
      <button v-if="user" @click="logout">Logout</button>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    user() {
      // console.log(this.$store.getters.user);
      return this.$store.getters.user;
    }
  },
  methods: {
    async logout() {
      try {
        await this.$store.dispatch({ type: "logout" });
        if (this.$route.fullPath !== "/") this.$router.push("/");
      } catch (err) {
        console.log("problem with logout", err);
      }
    }
  }
};
</script>
