<template>
  <section class="toy-edit" v-if="toyToEdit">
    <router-link to="/toy">Back</router-link>
    <div class="toy-details" v-if="toyToEdit">
      <form @submit.prevent="editToy()">
        <!-- <p> -->
        <validation-provider rules="required" v-slot="{ errors }">
          Name: 
          <input v-model="toyToEdit.name" type="text"/>
          <div>{{ errors[0] }}</div>
        </validation-provider>
        <!-- </p> -->
        <validation-provider rules="required" v-slot="{ errors }">
          Price: 
          <input v-model="toyToEdit.price" type="number"/>
          <div>{{ errors[0] }}</div>
        </validation-provider>
        <multi-select :toy="toyToEdit" @updateLabels="addLabels"></multi-select>

        <p v-if="toyToEdit._id">
          <span>Created At:</span>
          {{ timeToShow }}
        </p>
        <small v-if="toyToEdit._id">Id: {{ toyToEdit._id }}</small>
        <div>
          <button>{{ toyToEdit._id ? 'Update' : 'Add'}}</button>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";
import multiSelect from "../cmps/multiselect.cmp.vue";
import { ValidationProvider, extend } from "vee-validate";
import { required } from "vee-validate/dist/rules";
extend("required", {
  ...required,
  message: "This field is required"
});

export default {
  data() {
    return {
      toyToEdit: null,
      labels: []
    };
  },
  async created() {
    const { toyId } = this.$route.params;

    if (toyId) {
      try{
        const toy = await toyService.getById(toyId)
        this.toyToEdit = JSON.parse(JSON.stringify(toy));
      }
      catch(err){
        console.log('problem with getting toy(created-edit)', err);
      }
    } else {
      this.toyToEdit = toyService.getEmptyToy();
    }
  },
  methods: {
    async editToy() {
      if(!this.toyToEdit.name || !this.toyToEdit.price) return 
      if (!this.toyToEdit._id) this.toyToEdit.createdAt = Date.now();
      try{
        await this.$store.dispatch({ type: "saveToy", toy: this.toyToEdit })
        this.$router.push("/toy");
      }
      catch{
        console.log('problem with edit toy:'+this.toyToEdit, err);
      }
    },
    addLabels(labels) {
      this.toyToEdit.labels = labels;
    }
  },
  computed: {
    timeToShow() {
      return new Date(this.toyToEdit.createdAt)
        .toLocaleString()
        .substring(0, 17);
    },
    checkedLabels() {
      var checkedLabels = this.toyToEdit.labels.map(label => label.txt);
      return checkedLabels.join(", ");
    }
  },
  components: {
    multiSelect,
    ValidationProvider
  }
};
</script>

