import { storageService } from './async-storage.service.js';

import Axios from 'axios';
var axios = Axios.create({ withCredentials: true });

const USER_URL = 'http://localhost:3030/api/user/';

export const userService = {
  getUser,
  getUsers,
  deleteUser,
  updateUser,
};

window.userService = userService;
async function getUser(userId) {
  try{
    const user = await axios.get(USER_URL+userId);
    return user
  } catch (err) {
    console.log('problem with getting user', err);
}

}
async function getUsers() {
  try {
    const users = await axios.get(USER_URL);
    return users;
  } catch (err) {
      console.log('problem with getting users', err);
  }
}
async function deleteUser(user) {
    try {
        await axios.delete(USER_URL+user._id);
        return 'deleted'
      } catch (err) {
          console.log('problem with deleting user' +user.username, err);
      }
}
function updateUser() {}
