<template>
  <section v-if="users">
    <div v-for="user in users" :key="user._id">
      <div class="user-card">
        <div>Name: {{user.fullname}}</div>
        <div>Username: {{user.username}}</div>
        <div>Id: {{user._id}}</div>
        <div>Created at: {{(user.createdAt).substring(0,10)}}</div>
        <button v-if="!user.isAdmin" @click="remove(user)">Delete</button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  created(){
    this.$store.dispatch({ type: 'loadUsers' });

  },
  computed: {
    users() {
      return this.$store.getters.users;
    },
    user() {
      const user = this.$store.getters.user;
      if (!user || !user.isAdmin) this.$router.push("/login");
      return user;
    }
  },
  methods: {
    remove(user) {
      this.$store.dispatch({ type: "deleteUser", user });
    }
  }
};
</script>

