import { authService } from '../services/auth.service.js';

export const authStore = {
  strict: true,
  state: {
    user: null,
  },

  getters: {
    user(state) {
      return JSON.parse(JSON.stringify(state.user));
    },
  },

  mutations: {
    setUser(state, { user }) {
      state.user = user;
    },
    setLogout(state) {
      state.user = null;
    },
  },
  actions: {
    async addUser({ commit }, { user }) {
      commit({ type: 'setLoading', isLoading: true });
      try {
        console.log('user from store', user);
        const newUser = await authService.signup(user);
        commit({ type: 'setUser', user:newUser });

        return newUser;
      } catch (err) {
        console.log('Problem with addUser in store', err);
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async login({ commit }, { user }) {
      commit({ type: 'setLoading', isLoading: true });
      try {
        // console.log('user from store', user);
        const newUser = await authService.login(user);
        commit({ type: 'setUser', user:newUser });

        return newUser;
      } catch (err) {
        console.log('Problem with login in store', err);
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async logout({ commit }) {
      commit({ type: 'setLoading', isLoading: true });
      try {
        await authService.logout();
        commit({ type: 'setLogout'});
      } catch (err) {
        console.log('Problem with addUser in store', err);
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async loadUser({ commit }) {
      try {
        const user = authService.getLoggedinUser()
        commit({ type: 'setUser', user });

        // console.log('from stor',user);
        // return user

      } catch(err) {
        console.log('problem with getting loggedUser', err);
      }
    },
  },
};
