<template>
  <section class="toy-filter">
    <div class="filter">
      <select v-model="filterBy.status" @change="filter">
        <option value="all">All</option>
        <option value="inStock">In stock</option>
      </select>
      <input type="text" placeholder="Search Toy" v-model="filterBy.txt" @input="filter" />
      <el-select 
       @change="filter"
      v-model="filterBy.labels" multiple placeholder="Search labels"
      v-show="labels.length">
        <el-option 
        v-for="label in labels" 
        :key="label._id" 
        :label="label.name"
        :value="label.name"></el-option>
      </el-select>
    </div>
    <div class="sort">
      Sort:
      <select @change="setSort" v-model="sortBy">
        <option value="txt">Name</option>
        <option value="priceLow">Price (low to high)</option>
        <option value="priceHigh">Price (high to low)</option>
        <option value="date">Date</option>
      </select>
    </div>
  </section>
</template>

<script>

export default {
  data() {
    return {
      filterBy: {
        status: "all",
        txt: "",
        labels: []
      },
      showLabels: false,
      sortBy: "date"
    };
  },
  computed: {
    labels(){
      return this.$store.getters.getLabels

    }
  },
  methods: {
    filter() {
      this.$store.dispatch({
        type: "setFilter",
        filterBy: JSON.parse(JSON.stringify(this.filterBy))
      });

    },
    setSort() {
      this.$store.dispatch({ type: "setSort", sortBy: this.sortBy });
    }
  }
};
</script>

