<template>
  <section>
    <form @submit.prevent="login()">
      <validation-provider rules="required" v-slot="{ errors }">
        Username:
        <input v-model="user.username" type="text" />
        <div>{{ errors[0] }}</div>
      </validation-provider>
      <validation-provider rules="required" v-slot="{ errors }">
        Password:
        <input v-model="user.password" type="password" />
        <div>{{ errors[0] }}</div>
      </validation-provider>

      <div>
        <button>Add</button>
      </div>
    </form>
    <div>
           <router-link to="/signup">Signup</router-link>

    </div>
  </section>
</template>


<script>
import { ValidationProvider, extend } from "vee-validate";
import { required } from "vee-validate/dist/rules";
extend("required", {
  ...required,
  message: "This field is required"
});

export default {
  data() {
    return {
      user: {
        username: null,
        password: null
      }
    };
  },
  destroyed() {
    this.user = {
      username: null,
      password: null
    };
  },
  methods: {
    async login() {
      try {
        // console.log("this.user", this.user);
        const loggedIn = await this.$store.dispatch({
          type: "login",
          user: this.user
        });
        if (loggedIn) this.$router.push("/toy");
      } catch (err) {
        console.log("problem with add user" + this.user, err);
      }
    }
  },
  components: {
    ValidationProvider
  }
};
</script>

