import { toyService } from '../services/toy.service.js';

export const toyStore = {
  strict: true,
  state: {
    toys: [],
    filterBy: {
      txt: '',
      status: 'all',
      labels: [],
    },
    labels: [],
    isLoading: false,
    sortBy: 'date',
  },

  getters: {
    filteredToys(state) {
      let filteredToys = [...state.toys];

      if (state.filterBy.status === 'inStock') {
        filteredToys = state.toys.filter((toy) => toy.inStock);
      }

      filteredToys = filteredToys.filter((toy) =>
        new RegExp(state.filterBy.txt, 'i').test(
          toy.name
        )
      );

      if (state.filterBy.labels.length) {
        filteredToys = filteredToys.filter((toy) =>
          state.filterBy.labels.every((filterLabel) =>
            toy.labels.includes(filterLabel)
          )
        );
      }

      if (state.sortBy === 'txt') {
        filteredToys = filteredToys.sort((toyA, toyB) => {
          if (toyA.name.toLowerCase() < toyB.name.toLowerCase()) return -1;

          if (toyA.name.toLowerCase() > toyB.name.toLowerCase()) return 1;

          return 0;
        });
      } else if (state.sortBy === 'date') {
        filteredToys = filteredToys.sort(
          (toyA, toyB) => toyB.createdAt - toyA.createdAt
        );
      } else if (state.sortBy === 'priceLow') {
        filteredToys = filteredToys.sort(
          (toyA, toyB) => toyA.price - toyB.price
        );
      } else {
        filteredToys = filteredToys.sort(
          (toyA, toyB) => toyB.price - toyA.price
        );
      }
      return filteredToys;
    },
    isLoading({ isLoading }) {
      return isLoading;
    },
    getLabels(state) {
      return JSON.parse(JSON.stringify(state.labels));
    },
  },

  mutations: {
    setToys(state, { toys }) {
      state.toys = toys;
    },
    setLabels(state, { labels }) {
      state.labels = labels;
    },
    setLoading(state, { isLoading }) {
      state.isLoading = isLoading;
    },
    addToys(state, payload) {
      state.products.push(payload.product);
    },
    addToy(state, { toy }) {
      state.toys.push(toy);
    },
    updateToy(state, { toy }) {
      const idx = state.toys.findIndex((currToy) => currToy._id === toy._id);
      state.toys.splice(idx, 1, toy);
    },
    removeToy(state, { toyId }) {
      const idx = state.toys.findIndex((currToy) => currToy._id === toyId);
      state.toys.splice(idx, 1);
    },
    setFilter(state, { filterBy }) {
      state.filterBy = filterBy;
    },
    setSort(state, { sortBy }) {
      state.sortBy = sortBy;
    },
  },

  actions: {
    async loadToys({ commit }) {
      commit({ type: 'setLoading', isLoading: true });
      try{
       const toys = await toyService.query()
       commit({ type: 'setToys', toys });
      }
      catch(err){
        console.log('Problem with loading toys in store', err);
      }
      finally{
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async loadLabels({ commit }) {
      try{
        const labels = await toyService.getLabels()
        commit({ type: 'setLabels', labels })
      }
      catch(err){
        console.log('store had problem with loading labels', err);
      }
    },
    async saveToy({ commit, dispatch }, { toy }) {
      commit({ type: 'setLoading', isLoading: true })
      const type = toy._id ? 'updateToy' : 'addToy';
      try{
        const savedToy = await toyService.save(toy)
        commit({ type: type, toy: savedToy })
        return savedToy
      }catch(err){
        console.log(`store had problem with ${type}: `+toy, err);
      }
      finally{
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async updateToy({ commit, dispatch }, { toy }) {
      console.log('here(161:store)');
      commit({ type: 'setLoading', isLoading: true });
      try{
        const savedToy = await toyService.save(toy)
        commit({ type: 'updateToy', toy: savedToy });
        return savedToy;
      }
      catch(err){
        console.log(`store had problem with updating toy: `+toy, err);
      }
      finally{
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async removeToy({ commit, dispatch }, { toyId }) {
      commit({ type: 'setLoading', isLoading: true });
      try{
        await toyService.remove(toyId)
        commit({ type: 'removeToy', toyId });
      }
      catch(err){
        console.log(`store had problem with deleting toy: `+toyId, err);
      }
      finally{
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    setSort({ commit }, { sortBy }) {
      commit({ type: 'setSort', sortBy });
    },
    setFilter({ commit }, { filterBy }) {
      commit({ type: 'setFilter', filterBy });
    },
  },
};
