const AUTH_URL = 'http://localhost:3030/api/auth/';
const STORAGE_KEY = 'user';
const STORAGE_KEY_LOGGEDIN = 'loggedinUser'
var gWatchedUser = null;

import { socketService, SOCKET_EVENT_USER_UPDATED } from './socket.service'
import Axios from 'axios';
var axios = Axios.create({ withCredentials: true });

export const authService = {
  login,
  logout,
  signup,
  getLoggedinUser
};

;

window.authService = authService;

function getLoggedinUser() {
    return JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN))
}


async function login(user) {
  try {
    const authUser = await axios.post(AUTH_URL + 'login', user);
    const loggedUser = authUser.data;
    sessionStorage.setItem(STORAGE_KEY, JSON.stringify(loggedUser));
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(loggedUser));
    socketService.emit('set-user-socket', loggedUser._id);

    return loggedUser;
  } catch (err) {
    console.log('problem with login auth ', err);
  }
}

async function logout() {
  try {
    await axios.post(AUTH_URL + 'logout');
    sessionStorage.removeItem(STORAGE_KEY);
    sessionStorage.removeItem(STORAGE_KEY_LOGGEDIN);
    socketService.emit('unset-user-socket');
    return 'logout successfully';
  } catch (err) {
    console.log('problem with logout auth ', err);
  }
}

async function signup(user) {
  try {
    console.log('user in service' , user);
    const newUser = await axios.post(AUTH_URL + 'signup', user);
    const signedUser = newUser.data;
    sessionStorage.setItem(STORAGE_KEY, JSON.stringify(signedUser));
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(signedUser));
    socketService.emit('set-user-socket',signedUser);

    return signedUser;
  } catch(err){
    console.log('problem with signup auth ', err);

  }

}
