
<template>
  <section class="toy-app main-layout full">
    <!-- <h1>Toy</h1> -->
    <toy-filter></toy-filter>
    <router-link to="/toy/edit/" class="add-link">Add toy</router-link>
    <p v-if="isLoading">Loading...</p>
    <toy-list v-else :toys="toys"></toy-list>
  </section>
</template>

<script>
import toyFilter from "../cmps/toy-filter.cmp.vue";
import { eventBusService, showMsg } from "../services/event-bus.service.js";
import { toyService } from "../services/toy.service.js";
import toyList from "../cmps/toy-list.cmp.vue";

export default {
  created() {
    eventBusService.$on("removeToy", this.deleteToy);
  },
  destroyed() {
    eventBusService.$off("removeToy", this.deleteToy);
    const filter = {
      status: "all",
      txt: "",
      labels: []
    };
    this.$store.dispatch({
      type: "setFilter",
      filterBy: filter
    });
  },
  computed: {
    toys() {
      return this.$store.getters.filteredToys;
    },
    isLoading() {
      return this.$store.getters.isLoading;
    }
  },
  methods: {
    async deleteToy(toyId) {
      try {
        await this.$store.dispatch({ type: "removeToy", toyId });
        showMsg(`Toy deleted`);
      } catch (err) {
        console.log("problem with deleting toy", err);
      }
    },
    toggleAdd() {
      this.isAdd = !this.isAdd;
      if (this.isAdd) this.toyToAdd = toyService.getEmptyToy();
      else this.toyToAdd = null;
    }
  },
  components: {
    toyList,
    toyFilter
  }
};
</script>



