<template>
<section>

  <form @submit.prevent="addUser()">
    <validation-provider rules="required" v-slot="{ errors }">
      Full name:
      <input v-model="user.fullname" type="text" />
      <div>{{ errors[0] }}</div>
    </validation-provider>
    <validation-provider rules="required" v-slot="{ errors }">
      Username:
      <input v-model="user.username" type="text" />
      <div>{{ errors[0] }}</div>
    </validation-provider>
    <validation-provider rules="required" v-slot="{ errors }">
      Password:
      <input v-model="user.password" type="password" />
      <div>{{ errors[0] }}</div>
    </validation-provider>

    <div>
      <button>Add</button>
    </div>
  </form>
  <div>
      <router-link to="/login">Login</router-link>

  </div>
</section>
</template>


<script>
import { ValidationProvider, extend } from "vee-validate";
import { required } from "vee-validate/dist/rules";
extend("required", {
  ...required,
  message: "This field is required"
});

export default {
  data() {
    return {
      user: {
        fullname: null,
        username: null,
        password: null,
      }
    };
  },
  methods: {
    async addUser() {
      try {
        // console.log('this.user', this.user);
        await this.$store.dispatch({ type: "addUser", user: this.user });
        this.$router.push("/toy");
      } catch (err){
        console.log("problem with add user" + this.user, err);
      }
    }
  },
  components: {
    ValidationProvider
  }
};
</script>

